import React from 'react';
import Game from './game';

const GameList = ({games}) => {
    return (
        <div>
            {games.map((_key,i) => {
                return (
                    <div className='center mw6-ns'>
                        <Game name={games[i]}/>
                    </div>
                )
            })}
        </div>
    )
}


export default GameList;